import CustomSidebar from "components/common/CustomSideBar/CustomSideBar";
import React, { useEffect, useState } from "react";
import Menu from "components/user/Menu/Menu";

import "./AdminLayout.css";
import useSidebar from "stores/common/SidebarStore/SidebarStore";
import { useHistory, useLocation } from "react-router";
import { RoutesString } from "pages/routesString";
import authenticationService from "services/Authentication_service";
import { studentRole } from "constants/constants";

const AdminLayout = ({ children, isLogined }) => {
    const [stateSideBar, actionSideBar] = useSidebar();
    const history = useHistory();
    const isStudentRole = isLogined?.role?.name?.toLowerCase() === studentRole ? true : false;
    if(isStudentRole){
        history.push(RoutesString.ACCESS_DENIED);
    }
    useEffect(() => {
        if (location?.pathname === "/admin") {
            history.push(RoutesString.ADMIN_CHUONG_TRINH_DAO_TAO);
        }
    }, []);

    return (
        <div className="adminlayout__wrapper">
            <div
                className={`adminlayout__sidebar ${
                    stateSideBar?.isOpenSideBar ? "active" : ""
                }`}
            >
                <CustomSidebar
                    isOpenSideBar={stateSideBar?.isOpenSideBar}
                    setIsOpenSideBar={actionSideBar?.setIsOpenSideBar}
                />
            </div>
            <div
                className={`adminlayout__content ${
                    !stateSideBar?.isOpenSideBar ? "active" : ""
                }`}
            >
                <div className="adminlayout__content__header">
                    <Menu isLogined={isLogined} />
                </div>
                <div className="adminlayout__content__children">{children}</div>
            </div>
        </div>
    );
};

export default AdminLayout;
