import React, { useState } from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";
import { useTranslation } from "react-i18next";
import userApi from "api/user/userApi";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

const ForgotPassword = (props) => {
  const { t } = useTranslation();
  const { modal, toggle, handleInvalidSubmit } = props;
  const [email, setEmail] = useState("");
  const handleSubmitEmailForgotPassword = async (e) => {
    e.preventDefault();
    try {
      const response = await userApi.postForgotPassword(email);
      if(response?.status === 200){
        toast.info(t("CHECK_EMAIL"));
      }else{
        toast.error(t('ACCOUNT_NOT_FOUND'))
      }
    } catch (err) {
      toast.warning(t("RESPONSE_MESSAGE400"));
    }
    toggle();
  };
  return (
    <div>
      <Modal isOpen={modal} toggle={toggle}>
        <AvForm
          onValidSubmit={handleSubmitEmailForgotPassword}
          onInvalidSubmit={handleInvalidSubmit}
        >
          <ModalHeader toggle={toggle}>{i18n.t("ENTER_EMAIL")}</ModalHeader>
          <ModalBody>
            <AvField
              type="text"
              name="Email"
              id="Email"
              placeholder="Email"
              onChange={(e) => setEmail(e.target.value)}
              autoComplete="email"
              autoFocus
              validate={{
                required: {
                  value: true,
                  errorMessage: t("REQUIRED_FIELD_WARNING"),
                },
                email: {
                  value: true,
                  errorMessage: t("EMAIL_PATTERN_FIELD_WARNING"),
                },
              }}
            />
          </ModalBody>
          <ModalFooter>
            <Button type="submit" color="primary">
              {i18n.t("SUBMIT")}
            </Button>{" "}
            <Button color="secondary" onClick={toggle}>
              {i18n.t("CANCEL")}
            </Button>
          </ModalFooter>
        </AvForm>
      </Modal>
    </div>
  );
};

export default ForgotPassword;
