const { createStore, createHook } = require("react-sweet-state");

const SIDEBAR = "SidebarStore";
const initialState = {
  isOpenSideBar: true,
};
const actions = {
  setIsOpenSideBar: (valueState) => ({ setState }) => {
    setState({
      ...valueState,
    });
  },
};
const Store = createStore({
  initialState,
  actions,
  name: SIDEBAR,
});
const useSidebar = createHook(Store);
export default useSidebar;
