import React, { useState } from "react";
import {
  Card,
  Row,
  Col,
  Container,
  Jumbotron,
  FormText,
  FormGroup,
  Button,
} from "reactstrap";
import "./Registration.css";
import userApi from "api/user/userApi";
import { useTranslation } from "react-i18next";
import { AvForm, AvField } from "availity-reactstrap-validation";
import AuthenticationService from "services/Authentication_service";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { toast } from "react-toastify";
import { RoutesString } from "pages/routesString";
import Notification from "components/common/Notification/Notification";
import { validatePassword } from "utils/utils";

export default function Registration(props) {
  if (AuthenticationService.currentUserValue) {
    props.history.push("/");
  }
  const [user, setUser] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    username: "",
  });
  const [status, setStatus] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
})
  const { t } = useTranslation();
  const handleValidSubmit = async (e) => {
    setNotification(null);
    if(!validatePassword(String(user?.password))){
      setStatus("Password phải chứa ít nhất 8 kí tự, 1 chữ số và 1 chữ in hoa")
    //   setNotification({
    //     color: "danger",
    //     isOpen: true,
    //     messages: ['Password phải chứa ít nhất 8 kí tự, 1 chữ số và 1 chữ in hoa'],
    // });
    return;
    }
    setIsLoading(true);
    try {
      e.preventDefault();
      const response = await userApi.signup(user);
      if (response.message) {
        setIsLoading(false);
        setStatus("");
        toast.success(t("REGISTRATION_SUCCESS"));
      } else {
        setIsLoading(false);
        setStatus(t("RESPONSE_MESSAGE500"));
      }
    } catch (err) {
      if (err.response) {
        if (err.response.status === 403 || err.response.status === 401) {
          authenticationService.logout();
        } else if (err.response.status === 409) {
          setStatus(t("CONFLICT_REGISTATION"));
        } else {
          setStatus(t("RESPONSE_MESSAGE500"));
        }
      }
      setIsLoading(false);
    }
  };
  const handleInvalidSubmit = async (e) => {}; // eslint-disable-line

  return (
    <div>
      {isLoading && <LoadingBar />}
      <Container>
        <Row>
          <Col xs="12" sm="12" md="7" lg="7">
            <div className="titleHead">
              <h2>{t("UNIVERSITY_NAME").toUpperCase()}</h2>
              <h6>{t("SOFT_NAME")}</h6>
            </div>
          </Col>
          <Col xs="12" sm="12" md="5" lg="5">
            <Jumbotron className="box">
              <Card>
                <AvForm
                  onValidSubmit={handleValidSubmit}
                  onInvalidSubmit={handleInvalidSubmit}
                >
                  <h4>{t("SIGNUP").toUpperCase()}</h4>
                  <hr />
                  <FormGroup className="mt-5">
                    <AvField
                      type="text"
                      name="FirstName"
                      id="FirstName"
                      placeholder={t("FIRST_NAME")}
                      onChange={(e) =>
                        setUser({
                          ...user,
                          firstName: e.target.value,
                        })
                      }
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                        minLength: {
                          value: 1,
                          errorMessage: t("NAME_LENGTH_WARNING"),
                        },
                        maxLength: {
                          value: 12,
                          errorMessage: t("NAME_LENGTH_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormGroup className="mt-4">
                    <AvField
                      type="text"
                      name="LastName"
                      id="LastName"
                      placeholder={t("LAST_NAME")}
                      onChange={(e) =>
                        setUser({
                          ...user,
                          lastName: e.target.value,
                        })
                      }
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                        minLength: {
                          value: 1,
                          errorMessage: t("NAME_LENGTH_WARNING"),
                        },
                        maxLength: {
                          value: 12,
                          errorMessage: t("NAME_LENGTH_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormGroup className="mt-4">
                    <AvField
                      type="text"
                      name="Username"
                      id="Username"
                      placeholder={t("USERNAME")}
                      onChange={(e) =>
                        setUser({
                          ...user,
                          username: e.target.value,
                        })
                      }
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                        minLength: {
                          value: 1,
                          errorMessage: t("NAME_LENGTH_WARNING"),
                        },
                        maxLength: {
                          value: 12,
                          errorMessage: t("NAME_LENGTH_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormGroup className="mt-4">
                    <AvField
                      type="text"
                      name="Email"
                      id="Email"
                      placeholder="Email"
                      onChange={(e) =>
                        setUser({
                          ...user,
                          email: e.target.value,
                        })
                      }
                      autoComplete="email"
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                        email: {
                          value: true,
                          errorMessage: t("EMAIL_PATTERN_FIELD_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormGroup className="mt-4 mb-4">
                    <AvField
                      type="password"
                      name="password"
                      id="Password"
                      placeholder={t("PASSWORD")}
                      onChange={(e) =>
                        {
                          setStatus("")
                          setUser({
                          ...user,
                          password: e.target.value,
                        })}
                      }
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                        minLength: {
                          value: 8,
                          errorMessage: t("PASSWORD_LENGTH_WARNING"),
                        },
                        maxLength: {
                          value: 16,
                          errorMessage: t("PASSWORD_LENGTH_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormText color="danger">{status}</FormText>
                  <div className="text-center my-4">
                    <Button className="buttonOrange">
                      {t("REGISTRATION")}
                    </Button>
                  </div>
                </AvForm>
                {notification && <Notification
                      color={notification?.color}
                      isOpen={notification?.isOpen}
                      messages={notification?.messages}
                  />}
                <hr />
                <p className="text-right">
                  <a
                    className="btn_login text-warning"
                    href={RoutesString.LOGIN}
                  >
                    {t("LOGIN")}
                  </a>
                </p>
              </Card>
            </Jumbotron>
          </Col>
        </Row>
      </Container>
    </div>
  );
}
