export const isNotNull = (value) => {
    if (
        !value ||
        (typeof value === "string" && value?.toLocaleLowerCase() === "null")
    ) {
        return false;
    }
    return value;
};

export const formatTimestamp = (data) => {
    let d = new Date(data);
    const date = d.toISOString().split("T")[0];
    const time = d.toTimeString().split(" ")[0].replace(/:/g, ":");
    return `${date} ${time}`;
};

export const validatePassword = (pwd) => {
    const pattern = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/g;
    if (pattern.test(pwd)) {
        "password should contain at least 8 characters, one number and one special character";
        return true;
    }
    return false;
}

export const isErrors = (error) => {
    // eslint-disable-next-line
    console.log("🚀 ~ file: utils.js ~ line 28 ~ isErrors ~ error", error)
    if ( typeof(error) === "undefined" || error === null ) {
        return false;
    }
    if(typeof error === 'object' && error !== null){
        if(Object.keys(error).length){
            return true;
        }
    }
    return false;

}

export const validateAcademicYear = (pwd) => {
    const pattern = /^((19|20)\d{2})[-]((19|20)\d{2})$/g;
    if (pattern.test(pwd)) {
        // correct
        return true;
    }
    //incorrect
    return false;
}