import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import {
    Navbar,
    Nav,
    NavItem,
    NavLink,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Button,
} from "reactstrap";
import {
    FaKey,
    FaLanguage,
    FaSearch,
    FaSignInAlt,
    FaSignOutAlt,
    FaUserCircle,
} from "react-icons/fa";

import "./Menu.css";

import defaultAvatar from "assets/images/default-avatar.png";
import englandFlag from "assets/images/language-icon/england-flag.png";
import vietnamFlag from "assets/images/language-icon/vietnam-flag.png";

import { RoutesString } from "pages/routesString";
import changeLanguage from "./services/change-language";
import { useHistory } from "react-router";
import SearchHeader from "../SearchHeader/SearchHeader";
import { studentRole } from "constants/constants";

export default function Menu(props) {
    const { t } = useTranslation();
    const history = useHistory();
    const { isLogined } = props;

    const [dropdownOpen, setDropdownOpen] = useState(false);
    const [dropdownOpen1, setDropdownOpen1] = useState(false);

    const toggle = function () {
        setDropdownOpen((prevState) => !prevState);
    };

    const toggle1 = function () {
        setDropdownOpen1((prevState) => !prevState);
    };
    const srcImgAvt = isLogined?.avatar
        ? `${isLogined?.avatar}`
        : defaultAvatar;
    const isStudentRole = isLogined?.role?.name?.toLowerCase() === studentRole ? true : false;

    return (
        <Navbar expand="lg" className="navbar__container">
            <Nav navbar>
                <NavItem className="navbar__btn_home">
                    <NavLink
                        className="nav-link btn_home active"
                        onClick={() => history.push(RoutesString.WELCOME)}
                    >
                        {t("HOME")}
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className="nav-link"
                        onClick={() => history.push(isStudentRole ? RoutesString.COMMON_CTDT : RoutesString.ADMIN_DSCTDT)}
                    >
                        {t("EDUCATION_PROGRAM")}
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className="nav-link"
                        onClick={() =>
                            history.push(isStudentRole ? RoutesString.COMMON_DSSYLLABUS : RoutesString.ADMIN_SYLLABUS)
                        }
                    >
                        {t("SYLLABUS")}
                    </NavLink>
                </NavItem>
            </Nav>

            <Nav className="ml-auto" navbar>
                <NavItem>
                    {isStudentRole ? null : <SearchHeader />}
                </NavItem>{" "}
                &emsp;
                {!isLogined ? (
                    <NavItem>
                        <Button
                            className="login-btn"
                            outline
                            href={RoutesString.LOGIN}
                        >
                            <FaSignInAlt /> &nbsp; {t("LOGIN")}
                        </Button>
                    </NavItem>
                ) : (
                    <NavItem>
                        <Dropdown
                            isOpen={dropdownOpen}
                            toggle={toggle}
                            className="profile_dropdown"
                        >
                            <DropdownToggle
                                caret
                                className="options-btn profile_dropdown__toggle"
                            >
                                <img
                                    style={{ borderRadius: "50%" }}
                                    src={srcImgAvt}
                                    alt="avartar"
                                    className="avt_profile"
                                />{" "}
                                &nbsp;
                                {`${isLogined.lastName} ${isLogined.firstName}`}
                            </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem>
                                    <NavLink
                                        className="btn_dropdown"
                                        onClick={() =>
                                            history.push(RoutesString.PROFILE)
                                        }
                                    >
                                        <FaUserCircle /> {t("PROFILE")}
                                    </NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink
                                        className="btn_dropdown"
                                        onClick={() =>
                                            history.push(
                                                RoutesString.UPDATE_PROFILE
                                            )
                                        }
                                    >
                                        <FaKey /> {t("UPDATE_PROFILE")}
                                    </NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink
                                        className="btn_dropdown"
                                        href={RoutesString.LOGOUT}
                                    >
                                        <FaSignOutAlt /> {t("LOGOUT")}
                                    </NavLink>
                                </DropdownItem>
                            </DropdownMenu>
                        </Dropdown>
                    </NavItem>
                )}
                &emsp;
                <NavItem>
                    <Dropdown isOpen={dropdownOpen1} toggle={toggle1}>
                        <DropdownToggle
                            caret
                            outline
                            className="language-btn profile_dropdown__toggle"
                        >
                            <FaLanguage /> &nbsp; {t("LANGUAGE")}
                        </DropdownToggle>
                        <DropdownMenu right>
                            <DropdownItem>
                                <NavLink onClick={() => changeLanguage("en")}>
                                    <img
                                        src={englandFlag}
                                        height="20px"
                                        alt="england-flag"
                                    />{" "}
                                    &nbsp; {t("ENGLISH")}
                                </NavLink>
                            </DropdownItem>
                            <DropdownItem>
                                <NavLink onClick={() => changeLanguage("vn")}>
                                    <img
                                        src={vietnamFlag}
                                        height="20px"
                                        alt="vietnam-flag"
                                    />{" "}
                                    &nbsp; {t("VIETNAMESE")}
                                </NavLink>
                            </DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </NavItem>
            </Nav>
        </Navbar>
    );
}
