import {
    Button,
    Dropdown,
    DropdownToggle,
    Input,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";
import React, { useEffect, useState, useRef } from "react";
import monHocApi from "api/common/monHoc.api";
import chuongTrinhDaoTaoApi from "api/common/chuong-trinh-dao-tao.api";
import "./SearchHeader.css";
import i18n from "i18n/i18n";
import searchApi from "api/common/search.api";
import { GROUP_SUGGESTION } from "constants/constants";
import { useHistory } from "react-router";
import { RoutesString } from "pages/routesString";
const useDebounce = (value, timeout) => {
    const [state, setState] = useState(value);

    useEffect(() => {
        const handler = setTimeout(() => setState(value), timeout);
        return () => clearTimeout(handler);
    }, [value, timeout]);
    return state;
};
const SearchHeader = (props) => {
    const dropdownRef = useRef(null);
    const history = useHistory();
    const [suggestions, setSuggestions] = useState([]);
    const [searchTerm, setSearchTerm] = useState("");
    const [isOpenDropDown, setIsOpenDropDown] = useState(false);
    const searchValue = useDebounce(searchTerm, 500);
    const toggle = () => setIsOpenDropDown((isOpenDropDown) => !isOpenDropDown);
    const handleChange = (e) => {
        setSearchTerm(e?.target?.value);
    };
    const handleChosenSuggestion = (suggestion) => {
        if (suggestion) {
            if (suggestion?.group === GROUP_SUGGESTION.CHUONG_TRINH_DAO_TAO) {
                return RoutesString.ADMIN_QLCTDT?.replace(
                    ":id",
                    suggestion?.id
                );
            } else if (suggestion?.group === GROUP_SUGGESTION.SYLLABUS) {
                return RoutesString.ADMIN_SYLLABUS_DETAILS?.replace(
                    ":id",
                    suggestion?.id
                );
            }
            return RoutesString.ACCESS_DENIED;
        }
    };
    useEffect(() => {
        const pageClickEvent = (e) => {
            if (
                dropdownRef.current !== null &&
                !dropdownRef.current.contains(e.target)
            ) {
                setIsOpenDropDown(!isOpenDropDown);
            }
        };
        if (isOpenDropDown) {
            window.addEventListener("click", pageClickEvent);
        }

        return () => {
            window.removeEventListener("click", pageClickEvent);
        };
    }, [isOpenDropDown]);
    useEffect(() => {
        if (!isOpenDropDown && searchTerm) {
            setIsOpenDropDown(true);
        }
    }, [searchTerm]);
    useEffect(() => {
        (async () => {
            if (!searchValue) {
                setSuggestions([]);
                return;
            }
            const result = await searchApi.search({ search: searchValue });
            setSuggestions(result);
        })();
    }, [searchValue]);
    return (
        <div className="menu-container">
            <Input
                value={searchTerm}
                onChange={handleChange}
                placeholder={i18n.t("admin.common.SEARCH")}
            />
            <nav
                ref={dropdownRef}
                className={`search_header menu ${
                    isOpenDropDown ? "active" : "inactive"
                }`}
            >
                <ul>
                    {suggestions?.length ? (
                        suggestions.map((item, index) => (
                            <a
                                className="lis_item_wrapper"
                                href={handleChosenSuggestion(item)}
                            >
                                <li className="list_item" key={index}>
                                    <p className="item_title">{item?.title}</p>
                                    <p className="item_group">
                                        {i18n.t(`HEADERS.group.${item?.group}`)}
                                    </p>
                                </li>
                            </a>
                        ))
                    ) : (
                        <li className="not_result">{i18n.t("EMPTY")}</li>
                    )}
                </ul>
            </nav>
        </div>
    );
};

export default SearchHeader;
