import axiosClient from "api/common/axiosClient";
import CONFIG from "config/config";
import queryString from "query-string";
import { toast } from "react-toastify";

export default {
    getAll: async function (filter={}) {
        const query = queryString.stringify({ limit:-1, page:0,...filter });
        try {
            const url = `${CONFIG.API.CHUONG_TRINH_DAO_TAO}?${query}`;
            return await axiosClient.get(url);
            // eslint-disable-next-line
        } catch (error) { }
    },
    create: async function (data) {
        try {
            const url = `${CONFIG.API.CHUONG_TRINH_DAO_TAO}`;
            return await axiosClient.post(url, data);
        } catch (error) {
            return false;
        }
    },
    checkIsExist: async function (data) {
        const query = queryString.stringify({ ...data });
        const url = `${CONFIG.API.CHUONG_TRINH_DAO_TAO}/is-exits?${query}`;
        return await axiosClient.get(url);
    },
    create_details: async function (data) {
        const url = `${CONFIG.API.CHUONG_TRINH_DAO_TAO}/create-detail`;
        return await axiosClient.post(url, data);
    }
};
