import axiosClient from "api/common/axiosClient";
import CONFIG from "config/config";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { toast } from "react-toastify";

export default {
    getAll: async function (filter={}) {
        const query = queryString.stringify({limit:-1,page:0, ...filter });
        try {
            const url = `${CONFIG.API.MON_HOC}?${query}`;
            return await axiosClient.get(url);
        } catch (error) {
            toast.error(i18n.t("ERROR"));
        }
    },
    import: async (file) => {
        try {
            const formData = new FormData();
            formData.append("file", file);
            const url = `${CONFIG.API.MON_HOC}/import-data`;
            return await axiosClient.post(url, formData, {
                headers: { "Content-Type": "multipart/form-data" },
            });
        } catch (error) {
            // toast.error("ERROR");
            return { isError: true, message: error?.response?.data?.message || i18n.t("ERROR"), data: error?.response?.data };
        }
    },
};
