import axiosClient from "api/common/axiosClient";

const userApi = {
  login: (user) => {
    const url = "auth/login";
    return axiosClient.post(url, user);
  },
  signup: (user) => {
    const url = "auth/signup";
    return axiosClient.post(url, user);
  },
  updateProfile: (id, user) => {
    const url = "auth/" + id;
    return axiosClient.put(url, user);
  },
  changePassword: (id, user) => {
    const url = "auth/" + id + "/change-password";
    return axiosClient.put(url, user);
  },
  profile: () => {
    const url = "auth/profile";
    return axiosClient.get(url);
  },
  postForgotPassword: (email) => {
    const url = `auth/forgot-password?email=${email}`;
    return axiosClient.post(url);
  },
  getLinkResetPassword: (id) => {
    const url = `auth/forgot-password/${id}`;
    return axiosClient.get(url);
  },
  postResetPassword: (id, data) => {
    const url = `auth/forgot-password/${id}`;
    return axiosClient.post(url, data);
  },
};
export default userApi;
