import React, { useState } from "react";
import {
  Card,
  Row,
  Col,
  Container,
  Jumbotron,
  FormText,
  FormGroup,
  Button,
} from "reactstrap";
import "./Login.css";
import { useTranslation } from "react-i18next";
import { AvForm, AvField } from "availity-reactstrap-validation";
import AuthenticationService from "services/Authentication_service";
import ForgotPassword from "components/ForgotPassword/ForgotPassword";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { validatePassword } from "utils/utils";

export default function Login(props) {
  if (AuthenticationService.currentUserValue) {
    props.history.push("/");
  }
  const { t } = useTranslation();
  const [user, setUser] = useState({ email: "", password: "" });
  const [isLoading, setIsLoading] = useState(false);
  const [status, setStatus] = useState("");
  const [modal, setModal] = useState(false);
  const toggle = () => setModal(!modal);

  const handleValidSubmit = async (e) => {
    if(!validatePassword(String(user?.password))){
      setStatus("Password phải chứa ít nhất 8 kí tự, 1 chữ số và 1 chữ in hoa");
      return;
    }
    setIsLoading(true);
    try {
      e.preventDefault();
      const response = await AuthenticationService.login(user);
      if (response.token) {
        const { from } = props.location.state || { from: { pathname: "/" } };
        props.history.push(from);
      } else {
        if(response?.message === 'ACCOUNT_NOT_VERIFY'){
          setStatus(t("ACCOUNT_NOT_VERIFY"));
        }else{
          setStatus(t("RESPONSE_MESSAGE500"));
        }
        setIsLoading(false);
      }
    } catch (err) {
      if (err.response) {
        if (err.response.status === 403 || err.response.status === 401) {
          authenticationService.logout();
        } else if (err.response.status === 400) {
          setStatus(t("LOGIN_FAIL"));
        } else {
          setStatus(t("RESPONSE_MESSAGE500"));
        }
      }
      setIsLoading(false);
    }
  };
  const handleInvalidSubmit = (e) => {}; // eslint-disable-line
  return (
    <>
      {isLoading && <LoadingBar />}
      <Container>
        <Row>
          <Col xs="12" sm="12" md="7" lg="7">
            <div className="titleHead">
              <h2>{t("UNIVERSITY_NAME").toUpperCase()}</h2>
              <h6>{t("SOFT_NAME")}</h6>
            </div>
          </Col>
          <Col xs="12" sm="12" md="5" lg="5">
            <Jumbotron className="box">
              <Card className="cardD shadow">
                <AvForm
                  onValidSubmit={handleValidSubmit}
                  onInvalidSubmit={handleInvalidSubmit}
                >
                  <h4>{t("LOGIN").toUpperCase()}</h4>
                  <hr />
                  <FormGroup className="mt-5">
                    <AvField
                      type="text"
                      name="Email"
                      id="Email"
                      placeholder="Email"
                      onChange={(e) =>
                        setUser({ ...user, email: e.target.value })
                      }
                      autoComplete="email"
                      autoFocus
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                        email: {
                          value: true,
                          errorMessage: t("EMAIL_PATTERN_FIELD_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormGroup className="mt-4 mb-4">
                    <AvField
                      type="password"
                      name="password"
                      id="Password"
                      placeholder={t("PASSWORD")}
                      onChange={(e) =>
                        {setStatus('');setUser({ ...user, password: e.target.value })}
                      }
                      autoComplete="current-password"
                      validate={{
                        required: {
                          value: true,
                          errorMessage: t("REQUIRED_FIELD_WARNING"),
                        },
                      }}
                    />
                  </FormGroup>
                  <FormText color="danger">{status}</FormText>
                  <div className="text-center my-4">
                    <Button className="buttonOrange">{t("LOGIN")}</Button>
                  </div>
                </AvForm>
                <hr />
                <Container className="mb-2">
                  <div className="form_login_footer">
                    <div className="btn_ text-center">
                      <a
                        type="button"
                        className="register_btn text-warning"
                        onClick={toggle}
                      >
                        {t("FORGOT_PASSWORD")}
                      </a>
                    </div>
                    <div className="btn_ text-center">
                      <a
                        className="text-warning register_btn"
                        href="/registration"
                      >
                        {t("REGISTRATION")}
                      </a>
                    </div>
                  </div>
                </Container>
              </Card>
            </Jumbotron>
          </Col>
        </Row>
      </Container>
      <ForgotPassword
        modal={modal}
        toggle={toggle}
        handleInvalidSubmit={handleInvalidSubmit}
      />
    </>
  );
}
