import React from "react";
import { RoutesString } from "pages/routesString";
import "./NotFound.css";
import { useTranslation } from "react-i18next";

export default function NotFound({ pageStatus, title, content }) {
  const { t } = useTranslation();

  return (
    <div id="notfound">
      <div class="notfound">
        <div class="notfound-404">
          <h1>{pageStatus || 404}</h1>
        </div>
        <h2>{t(title || "404_TITLE")}</h2>
        <p>{t(content || "404_CONTENT")}</p>
        <a href={`${RoutesString.WELCOME}`}>{t("BACK_TO_HOME")}</a>
      </div>
    </div>
  );
}
