import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { FaAngleLeft } from "react-icons/fa";
import "./CustomSideBar.css";
import { Row } from "reactstrap";
import { useHistory, useLocation } from "react-router";
import { RoutesString } from "pages/routesString";
import menus from "pages/menuSidebar";
import authenticationService from "services/Authentication_service";

const CustomSidebar = ({ isOpenSideBar, setIsOpenSideBar }) => {
    const { t } = useTranslation();
    const history = useHistory();
    const location = useLocation();
    const [chooseIndex, setChooseIndex] = useState(null);
    const { currentUserValue } = authenticationService;
    const handleClickSidebarBtn = (index, to = "") => {
        if (chooseIndex === index) {
            setChooseIndex(-1);
        } else {
            setChooseIndex(index);
        }
        if (to) {
            history.push(to);
        }
    };
    useEffect(() => {
        const indexTab = menus.findIndex((item, index) => {
            const { pathname = "" } = location;
            if (pathname && item?.subMenu?.length) {
                if (
                    item?.subMenu?.find((subItem1) => subItem1.to === pathname)
                ) {
                    return true;
                }
            }
            return false;
        });
        setChooseIndex(indexTab);
    }, [location?.pathname]);
    return (
        <div className="sidebar__wrapper">
            <Row className="sidebar__title">
                <h5>{t("ADMIN_PAGES")}</h5>
                <span
                    className={isOpenSideBar ? "sidebar active" : "sidebar"}
                    onClick={() =>
                        setIsOpenSideBar({
                            isOpenSideBar: !isOpenSideBar,
                        })
                    }
                >
                    <FaAngleLeft className="icon_open" />
                </span>
            </Row>
            <div className="line"></div>
            <Row className="sidebar__content">
                <div className="sidebarmenu__wrapper">
                    <div className="sidebarmenu">
                        {menus.map((item, index) => {
                            const sidebarItemClassName =
                                chooseIndex === index
                                    ? "my-collapsible choose"
                                    : "my-collapsible";
                            return (
                                <div
                                    key={index}
                                    className="my-collapsible-container"
                                >
                                    <div
                                        className={`${sidebarItemClassName} ${
                                            item?.keyword?.includes(
                                                location?.pathname
                                            )
                                                ? "active"
                                                : ""
                                        }`}
                                    >
                                        <button
                                            className="btn__toggle"
                                            onClick={() =>
                                                handleClickSidebarBtn(
                                                    index,
                                                    item?.to
                                                )
                                            }
                                        >
                                            <span className="titlePages">
                                                {item?.title}
                                            </span>
                                            {item?.subMenu.length > 0 && (
                                                <span className="icon__chevron">
                                                    <i
                                                        className={`fa fa-chevron-circle-up icon`}
                                                    />
                                                </span>
                                            )}
                                        </button>
                                        <div className="my-collapsible__content">
                                            <div className="my-collapsible__content-inner">
                                                {item?.subMenu?.map(
                                                    (subItem, index) => (
                                                        <a
                                                            key={index}
                                                            className="subMenu_item"
                                                            onClick={() =>
                                                                history.push(
                                                                    subItem?.to
                                                                )
                                                            }
                                                        >
                                                            <div
                                                                className={`subMenu_item__title ${
                                                                    subItem?.to ===
                                                                    location?.pathname
                                                                        ? "active"
                                                                        : ""
                                                                }`}
                                                                key={index}
                                                            >
                                                                {subItem?.title?.toLowerCase()}
                                                            </div>
                                                        </a>
                                                    )
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                    <div className="line"></div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </Row>
            {/* <Row className="sidebar__content">
                <div className="sidebarmenu__wrapper">
                    <div className="sidebarmenu">
                        <div className="my-collapsible-container">
                            <div className="my-collapsible">
                                <button
                                    className={"titlePages btn__toggle"}
                                    onClick={() =>
                                        history.push(RoutesString.PROFILE)
                                    }
                                >
                                    <span>{t("MANAGE_ACCOUNT")}</span>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </Row> */}
        </div>
    );
};

export default CustomSidebar;
