import React from "react";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import useUILoading from "stores/common/UILoadingStore/UILoadingStore";

const LoadingContainer = (props) => {
  const [stateUILoading, actionUILoading] = useUILoading();
  return <>{stateUILoading.isLoading && <LoadingBar />}</>;
};

export default LoadingContainer;
