const { createStore, createHook } = require("react-sweet-state");

const UI_LOADING = "UILoadingStore";
const initialState = {
  isLoading: false,
};
const actions = {
  setIsLoading: (valueState) => ({ setState }) => {
    setState({
      ...valueState,
    });
  },
};
const Store = createStore({
  initialState,
  actions,
  name: UI_LOADING,
});
const useUILoading = createHook(Store);
export default useUILoading;
