import axios from "axios";
import queryString from "query-string";
import i18n from "i18n/i18n";
import configApi from "config/config";
import { useHistory } from "react-router-dom";
import { RoutesString } from "pages/routesString";
import { toast } from "react-toastify";
import { ROLES_MESSAGE } from "constants/constants";
const NODE_ENV = process.env.REACT_APP_NODE_ENV || "development";

const axiosClient = axios.create({
    baseURL: configApi.API.INDEX,
    headers: {
        "content-type": "application/json",
    },
    paramsSerializer: (params) => queryString.stringify(params),
});

axiosClient.interceptors.request.use(
    (config) => {
        if (config.url === RoutesString.LOGIN) {
            return config;
        }
        if (localStorage.getItem("token")) {
            const accessToken = JSON.parse(localStorage.getItem("token"))
                .accessToken;
            if (accessToken !== null) {
                config.headers["Authorization"] = `Bearer ${accessToken}`;
            }
        }
        return config;
    },
    (err) => {
        return Promise.reject(err);
    }
);

const handleRefreshToken = async (refreshToken) => {
    try {
        const response = await axiosClient.post(RoutesString.REFRESHTOKEN, {
            refreshToken,
        });
        return response;
    } catch (error) {
        throw error;
    }
};

axiosClient.interceptors.response.use(
    (response) => {
        if (response && response.data) {
            return response.data;
        }
        return response;
    },
    async (error) => {
        if (
            (error?.response?.data?.message === ROLES_MESSAGE.NO_PERMISTION ||
                error?.response?.data?.message === "AUTHORIZATION_ERROR") &&
            error?.response?.data?.statusCode === 403
        ) {
            return (window.location.href = RoutesString.ACCESS_DENIED);
            // Promise.reject(i18n.t("NO_PERMISTION"));
            // toast.error(i18n.t("NO_PERMISTION"));
        } else if (
            error?.response?.data?.message === ROLES_MESSAGE.UNAUTHORIZED &&
            error?.response?.data?.statusCode === 401
        ) {
            try {
                const { refreshToken = "" } = JSON.parse(
                    localStorage.getItem("token")
                );
                const response = await handleRefreshToken(refreshToken);
                localStorage.setItem(
                    "token",
                    JSON.stringify({
                        refreshToken: response.refreshToken,
                        accessToken: response.token,
                    })
                );
                error.response.config.headers[
                    `Authorization`
                ] = `Bearer ${response.token}`;
                return axios(error?.response?.config);
            } catch (error) {
                localStorage.clear();
                toast.error(i18n.t('TOKEN_INVALID'));
                return (window.location.href = RoutesString.LOGIN);
            }
        } else {
            return Promise.reject(error);
        }
    }
);

export default axiosClient;
