import React, { useState, useEffect } from "react";
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Container,
} from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";
import { useTranslation } from "react-i18next";
import { useParams, useHistory } from "react-router-dom";
import userApi from "api/user/userApi";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { toast } from "react-toastify";
import { RoutesString } from "pages/routesString";

const ResetPassword = (props) => {
  const { t } = useTranslation();
  const { id } = useParams();
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(true);
  const [newpassword, setNewPassword] = useState("");
  const [reNewpassword, setReNewPassword] = useState("");
  const [isExpiredLink, setIsExpiredLink] = useState(2);
  const handleInvalidSubmitResetPassword = async (e) => {
    e.preventDefault();
    try {
      const response = await userApi.postResetPassword(id, {
        password: newpassword,
        passwordConfirm: newpassword,
      });
      toast.success(response.message);
      history.push(RoutesString.LOGIN);
    } catch (err) {
      toast.error(t("RESPONSE_MESSAGE500"));
    }
  };
  const handleInvalidSubmit = (e) => {}; // eslint-disable-line
  useEffect(() => {
    async function callApi() {
      try {
        const response = userApi.getLinkResetPassword(id);
        setIsExpiredLink(0);
        setIsLoading(false);
      } catch (err) {
        setIsExpiredLink(1);
        setIsLoading(false);
      }
    }

    callApi();
  }, []);
  return (
    <>
      {isLoading && <LoadingBar />}
      {isExpiredLink === 0 && (
        <Card>
          <AvForm
            onValidSubmit={handleInvalidSubmitResetPassword}
            onInvalidSubmit={handleInvalidSubmit}
          >
            <CardHeader tag="h3">{t("RESET_PASSWORD")}</CardHeader>
            <CardBody>
              <AvField
                type="password"
                name="newpassword"
                id="NewPassword"
                placeholder="New password"
                onChange={(e) => setNewPassword(e.target.value)}
                autoFocus
                validate={{
                  required: {
                    value: true,
                    errorMessage: t("REQUIRED_FIELD_WARNING"),
                  },
                  minLength: {
                    value: 6,
                    errorMessage: t("PASSWORD_LENGTH_WARNING"),
                  },
                  maxLength: {
                    value: 16,
                    errorMessage: t("PASSWORD_LENGTH_WARNING"),
                  },
                }}
              />
              <AvField
                type="password"
                name="reNewpassword"
                id="ReNewPassword"
                placeholder="Re-type new password"
                onChange={(e) => setReNewPassword(e.target.value)}
                validate={{
                  required: {
                    value: true,
                    errorMessage: t("REQUIRED_FIELD_WARNING"),
                  },
                  minLength: {
                    value: 6,
                    errorMessage: t("PASSWORD_LENGTH_WARNING"),
                  },
                  maxLength: {
                    value: 16,
                    errorMessage: t("PASSWORD_LENGTH_WARNING"),
                  },
                  match: {
                    value: "newpassword",
                    errorMessage: t("PASSWORD_MATCH_WARNING"),
                  },
                }}
              />
            </CardBody>
            <CardFooter>
              <Button color="primary" type="submit">
                {t("CHANGE")}
              </Button>{" "}
            </CardFooter>
          </AvForm>
        </Card>
      )}
      {isExpiredLink === 1 && (
        <Container>
          <h1>{t("404_TITLE")}</h1>
        </Container>
      )}
    </>
  );
};

export default ResetPassword;
